section {
    display: grid;
    grid-template-columns:
        1fr
        min(1200px, 100%)
        1fr;
    padding-block: var(--size-400);
    & > * {
        grid-column: 2;
    }
    & .content {
        padding-inline: var(--size-100);
    }
}

.hero-layout {
    display: flex;
    flex-direction: column;
    padding-inline: var(--size-100);
    gap: var(--size-100);
    align-items: center;
    &:not(:last-child) {
        margin-block-end: var(--size-400);
    }
    & .hero-summary > p {
        font-size: var(--size-150);
    }
    & .hero-illustration {
        max-height: 20rem;
    }
}

@keyframes cloud-scrolling {
    from {
        background-position-x: -400px;
    }
    to {
        background-position-x: -2600px; 
    }
}

#summary {
    background-color: var(--swatch-orange-50);
    background-image: url("/games/townbox/data/clouds.png");
    background-position-x: -400px;
    background-repeat: repeat-x;
    animation-name: cloud-scrolling;
    animation-timing-function: linear;
    animation-duration: 60s;
    animation-iteration-count: infinite;

    & > div {
        padding-inline: var(--size-100);
        display: flex;
        flex-direction: column;
        align-items: center;
        & > h1 {
            text-align: center;
        }
        & > #steam-widget {
            margin-block-start: var(--size-200);
            width: 100%;
            max-width: 646px;
            height: 190px;
        }
    }
}

#hero {
    background-image: url("/games/townbox/data/hero-background.jpg");
    background-size: cover;
    & div {
        padding-inline: var(--size-100);
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    & .button {
        margin-block-end: var(--size-400);
    }
    & img {
        width: 100%;
        max-width: 40rem;
        margin-inline: auto;
        margin-block-end: var(--size-200);
    }
}

@keyframes pattern-anim {
    from {
        background-position-x: 0;
        background-position-y: 0;
    }
    to {
        background-position-x: -1145px;
        background-position-y: -1145px;
    }
}

#info {
    background-color: var(--swatch-yellow-200);
    background-image: url(/games/townbox/data/pattern-icons.png);
    animation-name: pattern-anim;
    animation-duration: 90s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    & .hero-layout {
        flex-direction: column-reverse;
    }
}

#gallery {
    background-color: var(--swatch-orange-50);
}

.screenshots-grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
    margin-block-end: var(--size-100);
    & img {
        width: 100%;
        border-radius: 2rem;
    }
}

@keyframes stripes-anim {
    from {
        background-position-x: 0;
    }
    to {
        background-position-x: -256px;
    }
}

#stay-in-the-loop {
    background-color: var(--swatch-blue-200);
    background-image: url("/games/townbox/data/pattern-stripes-alt2.png");
    background-blend-mode: screen;
    animation-name: stripes-anim;
    animation-duration: 10s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.youtube-embed {
    aspect-ratio: 16 / 9;
    width: 100%;
    border-radius: var(--size-200);
}

@media (prefers-reduced-motion) {
    #summary, #info, #stay-in-the-loop {
        animation-name: none;
    }
}

@media screen and (min-width: 1280px) {
    .hero-layout {
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 30rem;
        padding-inline: var(--size-100);
        gap: var(--size-400);
        align-items: center;
        & .hero-summary > p {
            font-size: var(--size-150);
        }
        & .hero-illustration {
            max-height: 30rem;
            justify-self: flex-end;
        }
    }
    
    .hero-layout:nth-child(even) {
        grid-template-columns: 30rem 1fr;
        & .hero-summary {
            grid-column: 2;
        }
        & .hero-illustration {
            grid-column: 1;
            grid-row: 1;
        }
    }
}