// Global CSS

// Font declarations
@font-face {
    font-family: 'Pally';
    src: url('/data/font/Pally-Regular.woff2') format('woff2'),
             url('/data/font/Pally-Regular.woff') format('woff');
    font-weight: 400;
    font-display: swap;
    font-style: normal;
}

@font-face {
    font-family: 'Pally';
    src: url('/data/font/Pally-Medium.woff2') format('woff2'),
             url('/data/font/Pally-Medium.woff') format('woff');
    font-weight: 500;
    font-display: swap;
    font-style: normal;
}

@font-face {
    font-family: 'Pally';
    src: url('/data/font/Pally-Bold.woff2') format('woff2'),
             url('/data/font/Pally-Bold.woff') format('woff');
    font-weight: 700;
    font-display: swap;
    font-style: normal;
}

@font-face {
    font-family: 'VideoDojo Icons';
    src: url('/data/font/videodojo.woff') format('woff');
    font-weight: 400;
    font-display: swap;
    font-style: normal;
}

:root {
    scroll-behavior: smooth;
    
    // Brand colors
    --swatch-vd-red: #FF6666;
    --swatch-vd-white: #FFFFFF;
    --swatch-vd-cream: #FFF9EF;
    --swatch-vd-ink: #10143E;

    // Color flags
    --color-text: var(--swatch-vd-ink);
    --color-accent: var(--swatch-vd-red);
    --color-container: transparent;

    // Sizing units
    --size-px: 1px;         // 1px
    --size-2px: 2px;        // 2px
    --size-25: 0.25rem;     // 4px
    --size-50: 0.5rem;      // 8px
    --size-75: 0.75rem;     // 12px
    --size-100: 1rem;       // 16px
    --size-125: 1.25rem;    // 20px
    --size-150: 1.5rem;     // 24px
    --size-200: 2rem;       // 32px
    --size-250: 2.5rem;     // 40px
    --size-300: 3rem;       // 48px
    --size-400: 4rem;       // 64px
    --size-500: 5rem;       // 80px
    --size-600: 6rem;       // 96px
    --size-700: 7rem;       // 112px
    --size-800: 8rem;       // 128px
    --size-900: 9rem;       // 144px
    --size-1000: 10rem;     // 160px
}

body {
    font-family: 'Pally', system-ui, -apple-system, sans-serif;
    font-size: var(--size-125); // More comfortable text-size
    line-height: 1.5; // Nicer line height
    color: var(--color-text);
    text-decoration-color: var(--color-accent);
}

a:any-link {
    color: inherit;
}

.icon {
    font-family: 'VideoDojo Icons';
    font-size: var(--size-150);
    font-weight: 400;
    color: inherit;
}

header, footer {
    display: grid;
    grid-template-columns:
        1fr
        min(1200px, 100%)
        1fr;
    padding-inline: var(--size-100);
    background: var(--swatch-vd-red);
    color: var(--swatch-vd-white);
    --color-accent: var(--swatch-vd-white);
}

:is(header, footer) > * {
    grid-column: 2;
}

header {
    padding-block: var(--size-100);
    & nav {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    & img {
        max-width: var(--size-500);
    }
    & menu {
        list-style: none;
        display: none;
        flex-direction: row;
        gap: var(--size-200);
        align-items: center;
        font-weight: bold;
        & > a:any-link {
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

footer {
    padding-block: var(--size-200);
    & nav {
        display: flex;
        flex-direction: column;
        gap: var(--size-200);
    }
    & .footer-logo-static {
        width: 100%;
        display: block;
    }
    & .footer-logo {
        display: none;
        max-width: 20rem;
        position: relative;
    }
    
    & .footer-logo-eye {
        position: absolute;
        left: 118px;
        top: 112px;
    }
    
    & .footer-logo-eye:last-of-type {
        position: absolute;
        left: 226px;
        top: 112px;
    }
    & menu {
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: column;
        gap: var(--size-50);
        align-items: flex-start;
        text-align: start;
        & .socials {
            display: flex;
            gap: var(--size-100);
            & a {
                text-decoration: none;
            }
        }
    }
}

details.dropdown {
    position: relative;
    & summary {
        list-style: none;
        cursor: pointer;
        & > .icon::before {
            content: '🗄️';
        }
    }
    & menu {
        --color-accent: var(--swatch-vd-red);
        background-color: var(--color-accent);
        display: flex;
        flex-direction: column;
        gap: 0;
        width: max-content;
        padding: 0.5rem 0;
        position: absolute;
        border-radius: 1rem;
        overflow: hidden;
        z-index: 1000;
        margin-block-start: 2rem;
        & .button {
            width: 100%;
            text-align: start;
            justify-content: flex-start;
            border-radius: 0;
        }
    }
    
}

details[open].dropdown > summary > .icon::before {
    content: '❌';
}

details.dropdown > summary::-webkit-details-marker {
    display: none;
}

section, article {
    background: var(--color-container);
}

.small-text {
    font-size: 0.75em;
}

@media screen and (min-width: 1280px) {
    header menu {
        display: flex;
    }

    header details.dropdown {
        display: none;
    }

    footer {
        padding-block: var(--size-400);
        & nav {
            display: grid;
            grid-template-rows: 1fr;
            grid-template-columns: 1fr repeat(2, 20rem);
            gap: 3rem;
        }
        & .footer-logo-static {
            display: none;
        }
        & .footer-logo {
            display: block;
            max-width: 20rem;
            position: relative;
            margin-block: -2rem;
        }
        
        & .footer-logo-eye {
            position: absolute;
            left: 118px;
            top: 112px;
        }
        
        & .footer-logo-eye:last-of-type {
            position: absolute;
            left: 226px;
            top: 112px;
        }
        & menu {
            list-style: none;
            display: flex;
            flex-direction: column;
            gap: var(--size-100);
            align-items: flex-end;
            text-align: end;
            justify-content: flex-start;
        }
    }
}