.will-fade {
    opacity: 0%;
    transform: translateY(1rem);
}

.fade-into-view {
    opacity: 100%;
    transform: none;
}

@media (prefers-reduced-motion: no-preference) {
    .will-fade {
        transition: opacity 1s ease, transform 1s ease;
    }
}